<template>
    <div>
      <swiper3 :key="looplist.length">
          <div class="swiper-slide" v-for="data in looplist" :key="data.bannerId">
            <img :src="data.imgUrl"/>
          </div>
      </swiper3>
        <tabsbar></tabsbar>
        <router-view></router-view>
    </div>
</template>
<script>
import tabsbar from '@/views/tabsbar'
import swiper3 from '@/views/swiper3'
import axios from 'axios'
export default {
  data () {
    return {
      looplist: []
    }
  },
  components: {
    tabsbar,
    swiper3
  },
  mounted () {
    axios({
      url: 'https://m.maizuo.com/gateway?type=2&cityId=310100&k=189822',
      method: 'get',
      headers: {
        'X-Client-Info': '{"a":"3000","ch":"1002","v":"5.0.4","e":"1582288622450971566184","bc":"310100"}',
        'X-Host': 'mall.cfg.common-banner'
      }
    }).then(res => {
      // console.log(res.data.data)
      this.looplist = res.data.data
    })
  }
}
</script>
<style lang="scss" scoped>
  .swiper-slide{
    img{
      width: 100%;
    }
  }
</style>
